.NavigationItem {
    width: 100%;
    height: 100%;   
}

.NavigationItem a{ 
    display: block;
    width: 100%;
    height: 100%;
    padding: 15px;
    color: white;
    text-decoration: none;
    box-sizing: border-box;
    font-size: 1.2rem;     
    transition: all 0.3s ease-out;  
}

.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.active{
    background-color: white;
    color: var(--dark-green);
    cursor: pointer;

}

 